  import * as React from "react";
import * as Showdown from "showdown";
import ReactMde, {ReactMdeTypes} from "../../../src/index";
import {storiesOf} from "@storybook/react";

interface State {
    mdeState: ReactMdeTypes.MdeState;
}

const icons = {
  "bold": (<strong>B</strong>),
  "heading": "H",
  "italic": (<em>I</em>),
  "strikethrough": (<del>S</del>),
  "link": "üîó",
  "quote-right": (<strong>‚Äù</strong>),
  "code": "ü§ì",
  "image": "üì∏",
  "list-ul": "‚è∫",
  "list-ol": "#Ô∏è‚É£",
  "tasks": "üìù",
};

const iconProvider = (name) => {
  return icons[name] || "‚ùì";
};

class EmojiIconsStory extends React.Component<{}, State> {
    converter: Showdown.Converter;

    constructor(props) {
        super(props);
        this.state = {
            mdeState: {
                markdown: "**Hello world!**",
            },
        };
        this.converter = new Showdown.Converter({
            tables: true,
            simplifiedAutoLink: true,
            strikethrough: true,
            tasklists: true,
        });
    }

    handleValueChange = (mdeState: ReactMdeTypes.MdeState) => {
        this.setState({mdeState});
    }

    render() {
        return (
            <ReactMde
                layout="horizontal"
                buttonContentOptions={{ iconProvider }}
                onChange={this.handleValueChange}
                editorState={this.state.mdeState}
                generateMarkdownPreview={(markdown) => Promise.resolve(this.converter.makeHtml(markdown))}
            />
        );
    }
}

storiesOf("Icon Customization", module).addDecorator(withStorySource(__STORY__, __ADDS_MAP__))
    .add("emojis", () => (
        <EmojiIconsStory/>
    ));

  